<template>
    <div>
        <transition-group name="el-fade-in-linear">
            <el-row id="news" :gutter="20" justify="end" key="action1" v-if="type == '1'">
                <transition-group name="el-fade-in-linear">
                    <el-col v-for="(item, index) in data" :lg="12" :md="21" :xl="12" :xs="24" :sm="24" :key="index"
                        ::key="index" v-show="isnotime(index)" class="mb-2">
                        <el-tooltip placement="auto" effect="light">
                            <template #content>
                                <span
                                    v-html="item.content.replaceAll('miHoYoGameJSSDK.openInBrowser', 'window.open').replaceAll('miHoYoGameJSSDK.openInWebview', 'window.open')"></span>
                            </template>
                            <el-countdown :value="getTime(index)" prefix="还有:">
                                <template #title>
                                    <img :src="item.banner">
                                    <el-button class="addto" type="danger" @click="addtoHome(item, index);" circle><el-icon>
                                            <Star />
                                        </el-icon></el-button>
                                    <span>{{
                                        item.title }}</span>
                                </template>
                            </el-countdown>
                        </el-tooltip>
                    </el-col>
                </transition-group>
                <el-col :span="24" style="height: 120px;">
                </el-col>
            </el-row>
            <el-row id="news" :gutter="20" justify="end" key="action2" v-if="type == '2'">
                <transition-group name="el-fade-in-linear">
                    <el-col v-for="(item, index) in data" :lg="12" :md="21" :xl="12" :xs="24" :sm="24" :key="index"
                        ::key="index" class="mb-2">
                        <el-tooltip placement="auto" effect="light">
                            <template #content>
                                <span
                                    v-html="item.content.replaceAll('miHoYoGameJSSDK.openInBrowser', 'window.open').replaceAll('miHoYoGameJSSDK.openInWebview', 'window.open')"></span>
                            </template>
                            <div class="newsbg">
                                <img :src="item.banner">
                                <el-button class="addto" type="danger" @click="addtoHome(item, index);" circle><el-icon>
                                        <Star />
                                    </el-icon></el-button>
                                <span>{{ item.title }}</span>
                            </div>
                        </el-tooltip>

                    </el-col>
                </transition-group>
                <el-col :span="24" style="height: 120px;">
                </el-col>
            </el-row>
            <el-row id="news" :gutter="20" justify="end" key="action3" v-if="type == '3'" v-infinite-scroll="getinfo">
                <transition-group name="el-fade-in-linear">
                    <el-col v-for="(item, index) in recommended_posts" :lg="12" :md="21" :xl="12" :xs="24" :sm="24"
                        :key="index" ::key="index" class="mb-2">

                        <el-tooltip placement="auto" effect="light">
                            <template #content>
                                <span v-html="item.post.content"></span>
                                <p> <b><a :href="'https://www.miyoushe.com/' + game + '/article/' + item.post.post_id"
                                            target="_blank">
                                            前往页面</a></b></p>

                            </template>
                            <div class="newsbg">
                                <img :src="item.image_list.length > 0 ? item.image_list[0].url : item.post.cover">
                                <el-button class="addto" type="danger" @click="addtoHome(item, index);" circle><el-icon>
                                        <Star />
                                    </el-icon></el-button>
                                <span>{{ item.post.subject }}</span>
                            </div>
                        </el-tooltip>

                    </el-col>
                </transition-group>
                <el-col :span="24" style="height: 120px;">
                </el-col>
            </el-row>
        </transition-group>

        <el-row id="banner">
            <el-col>
                <el-carousel trigger="click" height="180px">
                    <el-carousel-item v-for="(item ) in carousels" :key="item">
                        <a target="_blank" :href="'https://www.miyoushe.com/' + this.game + item.path">
                            <img :src="item.cover">
                        </a>
                    </el-carousel-item>
                </el-carousel>
            </el-col>
            <el-col>
                <el-tabs v-model="type" class="h-0 mt-3 p-0" stretch>
                    <el-tab-pane label="活动" name="1" />
                    <el-tab-pane label="全部" name="2" />
                    <el-tab-pane label="资讯" name="3" />
                </el-tabs>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import { Star } from '@element-plus/icons-vue'
import axios from 'axios';
import DefaultConfig from '../DefaultConfig.vue';
export default {
    name: 'GameInfoDynamicmiHoYo',
    watch: {
        '$route.params.game': function () {
            this.getwebHome();
        }
    },
    DefaultConfig,
    Star,
    data() {
        return {
            mihoyourl: DefaultConfig.mihoyourl,
            data: [],
            type: "1",
            value2: new Date("2023/10/10 14:59").getTime(),
            game: this.$route.params.game,
            carousels: [],
            dataurl: "",
            recommended_posts: [],
            pagenum: 1
        };
    },
    updated() {

    },
    mounted() {
        this.getwebHome();
        this.getactivity();

    },
    methods: {
        getwebHome() {
            var _this = this;
            axios
                .get("https://bbs-api-static.miyoushe.com/apihub/wapi/webHome?gids=" + this.gametoid() + "&page=" + this.pagenum + "&page_size=8")
                .then((response) => {
                    _this.carousels = response.data.data.carousels;
                    _this.recommended_posts = response.data.data.recommended_posts;
                });
        },
        getinfo() {
            var _this = this;
            this.pagenum += 1;
            axios
                .get("https://bbs-api-static.miyoushe.com/apihub/wapi/webHome?gids=" + this.gametoid() + "&page=" + this.pagenum + "&page_size=8")
                .then((response) => {
                    _this.recommended_posts = _this.recommended_posts.concat(response.data.data.recommended_posts);
                });

        },
        getactivity() {
            var _this = this;

            if (this.dataurl != "")
                axios
                    .get(this.dataurl)
                    .then((response) => {
                        _this.data = response.data.data.list;
                    });
        },
        isnotime(index) {
            var dummy = document.createElement("div");
            dummy.innerHTML = this.data[index].content;



            if (this.type == "1")
                if (dummy.innerText.split('<t class="t_lc">')[2] != null) {
                    if (dummy.innerText.split('<t class="t_lc">')[2].split("</t>")[0] != null) {
                        if (new Date(dummy.innerText.split('<t class="t_lc">')[2].split("</t>")[0]) > new Date())
                            return true;
                    }
                } else if (dummy.innerText.split('<t class="t_lc">').length > 1) {
                    return true;
                }

            if (this.type == "2") {
                return true;
            }


            return false;
        },
        getTime(index) {
            var dummy = document.createElement("div");
            dummy.innerHTML = this.data[index].content;
            if (dummy.innerText.split('<t class="t_lc">')[2] != null) {
                return new Date(dummy.innerText.split('<t class="t_lc">')[2].split("</t>")[0]).getTime();
            } else if (dummy.innerText.split('<t class="t_lc">').length > 1) {
                return new Date(dummy.innerText.split('<t class="t_lc">')[1].split("</t>")[0]).getTime();
            }
            return 0;
        },
        addtoHome(item, index) {
            if (this.type == "2" || this.type == "1") {
                item.content = item.content.replaceAll('miHoYoGameJSSDK.openInBrowser', 'window.open').replaceAll('miHoYoGameJSSDK.openInWebview', 'window.open');
            }
            item.local = {
                countdown: this.type == "1" ? this.getTime(index) : null,
                game: this.game,
                type: this.type,
            }
            if (localStorage.getItem("Homedata") != null) {
                var data = JSON.parse(localStorage.getItem("Homedata"));
                data = data.concat(item);
                localStorage.setItem("Homedata", JSON.stringify(data));
            } else {
                var data2 = [];
                data2 = data2.concat(item);
                localStorage.setItem("Homedata", JSON.stringify(data2));
            }

            this.$notify.success({
                message: '添加到主页成功',
                position: "top-left",
                duration: 1000,
                showClose: false
            })


        },
        gametoid() {
            if (this.game == "sr") {
                this.dataurl = this.mihoyourl.sr;
                return 6;
            }
            if (this.game == "ys") {
                this.dataurl = this.mihoyourl.ys;
                return 2;
            }
            if (this.game == "bh2") {
                this.type = "3"
                return 3;
            }
            if (this.game == "zzz") {
                this.type = "3"
                return 8;
            }
            if (this.game == "bh3") {
                if (this.type != "3")
                    this.type = "2"
                this.dataurl = this.mihoyourl.bh3;
                return 1;
            }
        },
    },
};
</script>

<style lang="scss" >
.el-popper {
    max-width: 50%;
    max-height: 80%;
    overflow: scroll;
    background: rgba(255, 255, 255, 0.74) !important;
    box-shadow: 0px 0px 40px 0px black;
    border: none !important;
    backdrop-filter: blur(5px);
}


.addto {
    position: absolute;
    top: 10px;
    right: 10px;
    box-shadow: 0px 0px 40px 0px black;
    background-color: #f56c6ca1;
    visibility: hidden;
    transition: all 0.3s;
    opacity: 0
}

#banner {
    width: 400px;
    position: fixed;
    left: 25px;
    bottom: 200px;
}

#news img {
    border-radius: 20px 20px 0px 0px;
}

#news span {
    color: white;
}

#news {
    position: absolute;
    right: 15px;
    top: 10px;
    width: 45%;
    padding: 10px;
}

#news .el-col:hover .addto {
    visibility: visible;
    opacity: 1
}

.newsbg {
    margin-bottom: 10px;
    box-shadow: 0px 0px 40px 0px black;
    border-radius: 20px;
    background-color: rgba(0, 0, 0, 0.514);
    backdrop-filter: blur(3px);
    height: 100%;
}

#news .el-statistic {
    margin-bottom: 10px;
    box-shadow: 0px 0px 40px 0px black;
    border-radius: 20px;
    background-color: rgba(0, 0, 0, 0.514);
    backdrop-filter: blur(3px);
    height: 100%;
}

#banner .el-carousel {
    box-shadow: 0px 0px 40px 0px black;
    border-radius: 20px;
}

#banner .el-tabs__header {
    backdrop-filter: blur(3px);
    background-color: rgba(0, 0, 0, 0.514);
    box-shadow: 0px 0px 40px 0px black;
    border-radius: 10px;
}

#banner .el-tabs__item.is-active {
    color: black !important;
    transition: all 0.3s;
}

#banner .el-tabs__active-bar {
    border-radius: 10px;
    height: 100%;
    z-index: -1;
    background-color: #fffffff0;
    backdrop-filter: blur(3px);
}
</style>